// @flow

import React from 'react'
import {Paper, Typography, Grid, Link} from '@material-ui/core'
import styled from 'styled-components'

type Props = {
  post: any,
}

const MyPaper = styled(Paper)`
  position: relative;
  background-color: ${props => props.theme.palette.grey[800]};
  color: ${props => props.theme.palette.common.white};
  margin-bottom: ${props => props.theme.spacing(4)}px;
  background-image: url(https://source.unsplash.com/random);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
`

const Overlay = styled.div`
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.3);
`

const Content = styled.div`
  position: relative;
  padding: ${props => props.theme.spacing(3)}px;

  ${props => props.theme.breakpoints.up('md')}: {
    padding: ${props => props.theme.spacing(6)}px;
    padding-right: 0;
  };
`

export default function MainFeaturedPost(props: Props) {
  const {post} = props

  return (
    <MyPaper style={{backgroundImage: `url(${post.image})`}}>
      {/* Increase the priority of the hero background image */}
      {<img style={{display: 'none'}} src={post.image} alt={post.imageText} />}
      <Overlay />
      <Grid container>
        <Grid item md={6}>
          <Content>
            <Typography component="h1" variant="h3" color="inherit" gutterBottom>
              {post.title}
            </Typography>
            <Typography variant="h5" color="inherit" paragraph>
              {post.description}
            </Typography>
            <Link variant="subtitle1" href="/">
              {post.linkText}
            </Link>
          </Content>
        </Grid>
      </Grid>
    </MyPaper>
  )
}
